import React from 'react';
import 'bootstrap-daterangepicker/daterangepicker.css';
import axios from 'axios';
import Cookies from 'js-cookie';

function DeleteWatchListButton(props) {
    const postDelete = () => {
        if (confirm('')){
            axios.defaults.xsrfCookieName = 'csrftoken'
            axios.defaults.xsrfHeaderName = 'X-CSRFToken'
    
            const headers = {
                headers: {
                  'X-CSRFToken': Cookies.get('csrftoken'),
                }
            }
            const data = {
                'watchlist_id': props.list_id,
            };
    
            axios.post(props.list_id + '/delete/', data, headers)
            .then(response => {
                alert('')
                props.getPost();
            })
            .catch(error => {
                if (error.response) {
                    //
                    console.log(error.response.data);
                } else if (error.request) {
                    //
                    console.log(error.request);
                } else {
                    //
                    console.log('Error', error.message);
                }
            })                
        }
    }

    return (
        <div className="position-relative">
            <button onClick={postDelete} className="delete_button btn w-auto h-auto"><span></span></button>   
        </div>
    )
}


export default DeleteWatchListButton