import React from 'react';
import ReactPaginate from 'react-paginate';

function Pagination(props) {

    const ONE_PAGE_DISPLAY_COUNT = 100;


    const calculatePageCount = () => {
        return Math.ceil(props.total_count / ONE_PAGE_DISPLAY_COUNT)
    };

    const handlePageClick = (e) => {
        props.setPage(e.selected + 1)
        console.log(e);
    };
  
    // 
    return (
        <div className="text-center">
            <ReactPaginate
                pageCount={calculatePageCount()} //
                marginPagesDisplayed={2} //
                pageRangeDisplayed={3} //
                onPageChange={handlePageClick} //
                containerClassName="pagination" //pageulclass
                pageClassName="page-item" //pageliclass
                pageLinkClassName="page-link" //pageaclass
                activeClassName="active" //activepageclass
                activeLinkClassName="active"
                previousLinkClassName="previous-link" 
                nextLinkClassName="next-link"
                previousLabel="<<" //previous
                nextLabel=">>" //next
                disabledClassName="disabled-button" //previousnext
                breakLabel=""  //page
            />
        </div>
    );
}
  
export default Pagination;